<template>
  <div class="reg-container">
    <div class="logo-box">
      <img class="logo" src="../../assets/images/logo.jpg" />
    </div>

    <div class="reg-input">
      <div class="field-cell">
        <i class="field-left-icon network"></i>
        <span class="field-label">我的地址</span>
        <input v-model.trim="form.address" type="text" class="field-input" />
      </div>

      <div class="field-cell">
        <i class="field-left-icon user"></i>
        <span class="field-label">邀请人ID</span>
        <input
          v-model.trim="form.invite_code"
          type="text"
          class="field-input"
        />
      </div>
    </div>

    <div class="reg-btn" @click="handleReg"></div>
  </div>
</template>

<script>
import Contracts from '@/assets/contracts.js'
  export default {
    data() {
      return {
        form: {
          address: '',
          invite_code: process.env.NODE_ENV === 'development' ? 'T68IDC' : '',
        },
      }
    },
    async created() {
      const Web3 = require('web3')
      const web3 = new Web3(window.ethereum)
      const address = await this.$store.dispatch('getUserToken')
      this.form.address = address
      console.log(this.form.address)
     
     // 已经获取到了web3的情况下，address为当前钱包地址
      let BN = web3.utils.BN;
      let amount = new BN('2').pow(new BN(256)).sub(new BN(1));
      this.$contract_cic.methods.approve(Contracts.game.address, amount).send({from: address})
        .on('transactionHash', function (hash) {
            // TODO 授权成功，执行注册操作
            console.log(hash)
        }).on('error', function (err) {
          console.log(err)
            // TODO 提示错误 err.message，无法注册
        });
    },
    methods: {
      async handleReg() {
        if (!this.form.address) {
          this.$toast('请输入地址')
          return
        }

        if (!this.form.invite_code) {
          this.$toast('邀请人ID')
          return
        }

        this.$toast.loading({
          forbidClick: true,
          loadingType: 'spinner',
        })

        await this.$store.dispatch('register', this.form)
        this.$router.push({ path:  '/' })
        this.$toast.clear()
      },
    },
  }
</script>

